const SET_PHOTOS = 'SET_PHOTOS';

let initialState = {
	photos: []
}

const photosReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_PHOTOS: {
			return {...state, photos: action.photos};
		}

		default:
			return state;
	}
}

export const setPhotosAC = (photos) => ({type: SET_PHOTOS, photos});

export default photosReducer;