import React from 'react';
import {loginApi, loginWithToken} from './../api.js'
import {Redirect} from 'react-router-dom'

const Redir = (props) => {
		let red = '';
			if (props.location.search != '') {
				red = props.location.search.split('='); 
			}
			loginWithToken(red).then(data => {
				localStorage.setItem('id', data.user_id)
				localStorage.setItem('key', data.access_token)
				localStorage.setItem('login', true)
				props.setLogin(true);
				console.log(data);
				console.log(data.user_id);
			})
		return ( <div>
			{!localStorage.getItem('login')
				? <div id='login'>
					<h3>Для пользования ресурсом необходимо авторизоваться</h3>
					<button onClick={loginApi}>Войти</button>
				</div>
				: <Redirect to='/search' />
			}
			</div>
		)
}

export default Redir;