import React from 'react';
import {Route} from 'react-router-dom';
import './App.css';
import HomeContainer from './HomeContainer/HomeContainer';
import SearchContainer from './SearchContainer/Searchcontainer';
import UserProfileContainer from './UserProfileContainer/UserProfileContainer';
import PhotoContainer from './PhotoContainer/PhotoContainer';
import FriendsContainer from './FriendsContainer/FriendsContainer';
import FollowersContainer from './FollowersContainer/FollowersContainer';
import VideoContainer from './VideoContainer/VideoContainer';
import PagesContainer from './PagesContainer/PagesContainer';
import LoginContainer from './LoginContainer/LoginContainer';
import RedirContainer from './RedirContainer/RedirContainer';
import {country_data} from './redux/state';


const App = (props) => {
  //debugger;
  return (
    <div className="wrap">
      <Route path='/home' render={() => <HomeContainer />} />
      <Route path='/redir' render={() => <RedirContainer />} />
      <Route path='/' render={() => <LoginContainer />} />
      <Route path='/search' render={() => <SearchContainer country_data={country_data} />} />
      <Route path='/user/:userId' render={() => <UserProfileContainer />} />
      <Route path='/photos/:userId' render={() => <PhotoContainer/>} />
      <Route path='/friends/:userId' render={() => <FriendsContainer/>} />
      <Route path='/followers/:userId' render={() => <FollowersContainer/>} />
      <Route path='/videos/:userId' render={() => <VideoContainer/>} />
      <Route path='/pages/:userId' render={() => <PagesContainer/>} />                                        
    </div>
    )
}


export default App;
