import React, {useState, useEffect} from 'react';
import './Search.css';
import User from './../User/User';
import DataContainer from './../DataContainer/DataContainer';
import Country from './../Country/Country';
import HeaderContainer from './../HeaderContainer/HeaderContainer';
import Input from './../Input/Input';
import getQuery from './../api'
import loader from './../loader.gif'
import {Redirect} from 'react-router-dom'
import {TransitionGroup, CSSTransition} from 'react-transition-group'

const Search = (props) => {
		let [load, setLoad] = useState(false);
		let [id_country, setCid] = useState('');
		let [hide, setHide] = useState(false);
		let [isLogin, setIsLogin] = useState(false);
		let [isLoad, setIsLoad] = useState(false);
		let [data, setData] = useState([]);
		let [cityName, setCity] = useState('');
		let user_id = React.createRef();
		let show = React.createRef();

	useEffect(() => {
		if (!localStorage.getItem('login')) {
			setIsLogin(true);
		} else {
			let method = 'users.search';
			let params = `q=${props.search.input_name}&country=${id_country}&city=${props.cityId.city_id}&count=50&fields=photo_100,first_name,last_name,city,country`;
			getQuery(method, params).then(data => {
				props.setUsers(data.response.items);
				setIsLogin(true);
				setIsLoad(true);
				setData(data.response.items);
			})
			document.title = 'Поиск пользователей';
		}
	}, [data.count])

	let user_link = (e) => {
		let id_user = e.currentTarget.getAttribute('id');
		console.log(id_user);
		props.setUserId(id_user);
	}



	let inputs = (e) => {
		let input_name = e.currentTarget.value;
		props.setInputName(input_name);
	}

	let i_city = (e) => {
		let city = e.currentTarget.value;
		props.setCity(city);
		setCity(city);
		setLoad(true);
	}

	let inputs1 = (e) => {
		let country = e.currentTarget.value;
		props.setCountry(country);
	}

	let loadCountry = () => {
		//debugger
		for (let i = 0; i < props.country_data.length; i++) {
			if (props.country.country.toLowerCase() === props.country_data[i].title.toLowerCase()) {
				setCid(props.country_data[i].id);
			}
		}
	}

	let showMenu = (e) => {
		!hide ? setHide(true) : setHide(false);
	}


	let usersLoad = (e) => {
		e.preventDefault();
		if (!localStorage.getItem('login')) {
			setIsLogin(true);
		} else {
			let method = 'users.search';
			let params = `q=${props.search.input_name}&country=${id_country}&city=${props.cityId.city_id}&count=50&fields=photo_100,first_name,last_name,city,country`;
			getQuery(method, params).then(data => {
				props.setUsers(data.response.items);
				setIsLogin(true);
				setIsLoad(true);
				setData(data.response.items);
			})
		}

	}
		
		return( <> {!localStorage.getItem('login') ? <Redirect to='/' /> 
			: <><HeaderContainer {...props} />
				<div className="search">
					<div className="input">
						<Input {...props} inputs={inputs} usersLoad={usersLoad} />
					</div>
					<div className="button">
						<Country {...props} show = {show} showMenu = {showMenu} hide={hide}
													inputs1 = {inputs1} loadCountry = {loadCountry} i_city = {i_city} />
					</div>
					{load && <DataContainer usersLoad={usersLoad} {...props} cityName={cityName}  id_country={id_country} />}
					{isLoad 
						? <TransitionGroup component='div'>{ props.users.users.map((item) => { return <CSSTransition
																		key={item.id}
																		classNames={'users'}
																		timeout={1000}
																		>
																			<User name = {item.first_name + " " + item.last_name}
																		user_id = {user_id}
																		user_link={user_link}
																		id = {item.id}
																		online = {item.online}
																		photo = {item.photo_100}
																		is_closed = {item.is_closed}
																		city = {'city' in item ? item.city.title : 'не указан'}/></CSSTransition> }) }</TransitionGroup> 
						: <p id="loader"><img src={loader} /></p>
					}
				</div>
			</>
			}
			</>
		)
}


export default Search;