const SET_ID = 'SET_ID';
const SET_DATA = 'SET_DATA';

let initialState = {
	id: '',
	data: []
}

const idLoginReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_ID: {
			return {...state, id: action.id};
		}
		case SET_DATA: {
			return {...state, data: action.data};
		}

		default:
			return state;
	}
}

export const setIdAC = (id) => ({type: SET_ID, id});
export const setDataAC = (data) => ({type: SET_DATA, data});

export default idLoginReducer;