import React from 'react';
import './../Search/Search.css';

const Input = (props) => {
	return (
		<form>
			<input id="input_name" value={props.value} onBlur={props.usersLoad} onChange={props.inputs} placeholder ="Введите имя" type="text" name=""/>
			<input type="submit" className="but" onClick={props.usersLoad} value="Найти" />
		</form>
		)
}

export default Input;