import React from 'react';


const Wall = (props) => {
	var UnixTime = props.date;
	var dat = new Date(UnixTime*1000);
	const options = {
		year: 'numeric',
		month: 'long',
		day: 'numeric',
		hour: 'numeric',
		minute: 'numeric',
		second: 'numeric'
	}

	var time = new Intl.DateTimeFormat('ru-RU', options).format(dat)
	return (
		<div style={{margin:20}}>
			<p style={{color:'#69E563'}}>{time}</p>
			<img src={props.photo} alt=""/>
			<p>{props.text}</p>
			<hr></hr>
		</div>
		)
}

export default Wall;