import React, {useState, useEffect} from 'react';
import Users from './../Users/Users';
import Wall from './../Wall/Wall';
import getQuery from './../api'
import loader from './../loader.gif'
import {Redirect} from 'react-router-dom'
import {NavLink} from 'react-router-dom'

const Userprofile = (props) => {
		let [isLoad, setLoad] = useState(false);
		let [isLogin, setLogin] = useState(false);
		let [data, setData] = useState([]);
		let [wallData, setWallData] = useState([]);

		useEffect(() => {
			if (localStorage.getItem('key')) {
				let method = 'users.get';
				let params = `user_ids=${props.userId.user_id || props.match.params.userId}&fields=is_closed,photo_max_orig,online,bdate,city,contacts,counters,country,last_seen,sex,status`;
				getQuery(method, params).then(data => {
						props.setUserInfo(data.response[0]);
						setLoad(true);
						setLogin(true);
						setData(data.response[0])
						document.title = props.userInfo.user_info.first_name + ' ' + props.userInfo.user_info.last_name;
					})
			} else {
				setLogin(false);
			}
		}, [data.online]);

		useEffect(() => {
			if (localStorage.getItem('key')) {
				let method = 'wall.get';
				let params = `owner_id=${props.userId.user_id || props.match.params.userId}&count=100&extended=1`;
				getQuery(method, params).then(data => {
					props.setWall(data.response);
					setLoad(true);
					setLogin(true);
					setWallData(data.response);
					})
			} else {
				setLogin(false);
			}
			}, [wallData.count]);

		if(!props.userInfo.user_info) return null;
		if(!props.wall.wall.items) return null;
		
		console.log(props.wall.wall);
		console.log(props.userInfo.user_info);
	return(
		<> {!localStorage.getItem('login')
			? <Redirect to='/' />
			: isLoad 
				? <div><div id='home'><NavLink to='/home'>На главную</NavLink></div>
				<div className="user_profile"><div>{<Users user = {props.userInfo.user_info}/>}</div></div>
				<div id='noteName' style={{marginTop:250}}>
					<h3 style={{textAlign:'center', marginBottom:100}}>Записи пользователя {props.userInfo.user_info.first_name + ' ' + props.userInfo.user_info.last_name}</h3>
					{props.wall.wall.items.filter(elem => elem.attachments)
					.filter(el => el.attachments[0].photo)
					.map((item) => {return <Wall key={item.id} photo={item.attachments[0].photo.sizes[2].url} text={item.text} date={item.date} />})}
				</div> 
				</div>
				: <p id="loader"><img src={loader} /></p>
			}
		</>	)
}

export default Userprofile;