const SET_FOLLOWERS = 'SET_FOLLOWERS';

let initialState = {
	followers: []
}

const followersReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_FOLLOWERS: {
			return {...state, followers: action.followers};
		}

		default:
			return state;
	}
}

export const setFollowersAC = (followers) => ({type: SET_FOLLOWERS, followers});

export default followersReducer;