import React, {useState, useEffect} from 'react';
import './Photo.css';
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";
import getQuery from './../api'
import loader from './../loader.gif'
import {Redirect} from 'react-router-dom'
import {NavLink} from 'react-router-dom'

const Photo = (props) => {
	let [isLoad, setIsLoad] = useState(false);
	let [data, setData] = useState([]);
	
		useEffect(() => {
			setIsLoad(true);
		let method = 'photos.getAll';
		let params = `owner_id=${props.userId.user_id || props.match.params.userId}&count=200&extended=1`;
		getQuery(method, params).then(data => {
			props.setPhotos(data.response.items);
			setIsLoad(false);
			setData(data.response.items)
			document.title = 'Фотографии пользователя ' + props.userInfo.user_info.first_name + ' ' + props.userInfo.user_info.last_name;
		}).catch(error => {
				console.log(error);
			})
		});
		//debugger;
		if(!props.photos.photos) return null;
		let res = props.photos.photos;
		const images = [];
			for (let i = 0; i < res.length; i++) {
				let one = res[i].sizes[6] ? res[i].sizes[6].url : '';
				let two = res[i].sizes[1] ? res[i].sizes[1].url : '';
				images[i] = {'original': one, 'thumbnail': two};
			} 
		console.log(res);
	return(
		<div className="photo">
			{!localStorage.getItem('login') 
			? <Redirect to='/' />
			: isLoad 
				? <div><div id='home'><button onClick={props.history.goBack}>&#60; Назад</button>
					<NavLink to='/home'>На главную</NavLink>
				</div>
					<ImageGallery items={images} />
				</div>
			   : <p id="loader"><img src={loader} /></p>
			   }
		</div>
			)
}

export default Photo;