import React from 'react';
import {loginApi} from './../api.js'
import {Redirect} from 'react-router-dom'


const Login = (props) => {
	return (
		<>{!localStorage.getItem('login')
				? <div id='login'>
					<h3>Для пользования ресурсом необходимо авторизоваться</h3>
					<button onClick={loginApi}>Войти</button>
				</div>
				: <Redirect to='/search' />
			}
		</>
		)
}

export default Login;