import {connect} from 'react-redux';
import Search from './../Search/Search';
import {setUsersAC} from './../redux/usersReducer'
import {setUserIdAC} from './../redux/userIdReducer'
import {setInputNameAC} from './../redux/searchReducer'
import {setCityAC} from './../redux/cityReducer'
import {setCountryAC} from './../redux/countryReducer'
import {setCityIdAC} from './../redux/cityIdReducer'

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setUsers: (users) => {
    	dispatch(setUsersAC(users));
    },
    setUserId: (user_id) => {
    	dispatch(setUserIdAC(user_id));
    },
    setInputName: (input_name) => {
    	dispatch(setInputNameAC(input_name));
    },
    setCity: (input_name) => {
    	dispatch(setCityAC(input_name));
    },
    setCityId: (city_id) => {
    	dispatch(setCityIdAC(city_id));
    },
    setCountry: (city_id) => {
    	dispatch(setCountryAC(city_id));
    }
  }
}

const SearchContainer = connect(mapStateToProps, mapDispatchToProps)(Search);

export default SearchContainer;