import React, {useState, useEffect} from 'react';
import User from './../User/User'
import './Followers.css'
import getQuery from './../api'
import loader from './../loader.gif'
import {Redirect} from 'react-router-dom'
import {NavLink} from 'react-router-dom'

const Followers = (props) => {
		let [isLoad, setIsLoad] = useState(false);
		let [data, setData] = useState([]);
		useEffect(() => {
			setIsLoad(false);
			let method = 'users.getFollowers';
			let params = `user_id=${props.userId.user_id || props.match.params.userId}&count=100&fields=photo_100,online`;
			getQuery(method, params).then(data => {
				props.setFollowers(data.response.items);
				setIsLoad(true);
				setData(data.response.items);
				document.title = 'Подписчики пользователя ' + props.userInfo.user_info.first_name + ' ' + props.userInfo.user_info.last_name;
			})
		}, [data.count]);
		let user_link = (e) => {
			let id_user = e.currentTarget.getAttribute('id');
			console.log(id_user);
			props.setUserId(id_user);
		}
		if(!props.followers.followers) return null;
		let res = props.followers.followers;
		console.log(res);
	return(
		<div className="followers">
			{!localStorage.getItem('login')
			? <Redirect to='/' />
			: isLoad ? <div className="search_user">
			<div id='home'><button onClick={props.history.goBack}>&#60; Назад</button>
					<NavLink to='/home'>На главную</NavLink>
				</div>
			<div>{ res.map((item) => { return <User key={item.id} name = {item.first_name + " " + item.last_name}
																		id = {item.id}
																		user_link = {user_link}
																		photo = {item.photo_100}
																		is_closed = {item.is_closed}
																		city = {'city' in item ? item.city.title : 'не указан'}/> }) }</div>
			</div>
		: <p id="loader"><img src={loader} /></p>
		}
		</div>
			)
}

export default Followers;