const SET_VIDEOS = 'SET_VIDEOS';

let initialState = {
	videos: []
}

const videosReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_VIDEOS: {
			return {...state, videos: action.videos};
		}

		default:
			return state;
	}
}

export const setVideosAC = (videos) => ({type: SET_VIDEOS, videos});

export default videosReducer;