import React, {useState, useEffect} from 'react';
import User from './../User/User'
import './Friends.css'
import getQuery from './../api'
import loader from './../loader.gif'
import {Redirect} from 'react-router-dom'
import {NavLink} from 'react-router-dom'

const Friends = (props) => {
	let [isLoad, setIsLoad] = useState(false);
	let [data, setData] = useState([])
	let [online, setOnline] = useState(false);
	useEffect(() => {
		let method = 'friends.get';
		let params = `user_id=${props.userId.user_id || props.match.params.userId}&count=50&fields=photo_100,online,city`;
		getQuery(method, params).then(data => {
			props.setFriends(data.response.items);
			setIsLoad(true);
			setData(data.response.items);
			document.title = 'Друзья пользователя ' + props.userInfo.user_info.first_name + ' ' + props.userInfo.user_info.last_name;
		})
	}, [data.count])

		let user_link = (e) => {
			let id_user = e.currentTarget.getAttribute('id');
			console.log(id_user);
			props.setUserId(id_user);
		}

		let allFriends = () => {
			setOnline(false);
		}

		let onlineFriends = () => {
			setOnline(true);
		}

		if(!props.friends.friends) return null;
	return(
		<div className="friends">
			{!localStorage.getItem('login') 
			? <Redirect to='/' />
			: isLoad ? <div className="search_user">
			<div id='home'><button onClick={props.history.goBack}>&#60; Назад</button>
					<NavLink to='/home'>На главную</NavLink>
				</div>
			<div id='onlineF'>
				<button onClick={allFriends}>Все</button>
				<button onClick={onlineFriends}>Онлайн</button>
			</div>
			<div>{!online 
				? props.friends.friends.map((item) => { return <User key={item.id} name = {item.first_name + " " + item.last_name}
																		id = {item.id}
																		user_link = {user_link}
																		photo = {item.photo_100}
																		online = {item.online}
																		is_closed = {item.is_closed}
																		city = {'city' in item ? item.city.title : 'не указан'}/> }) 
				: props.friends.friends.map((item) => { return item.online === 1 && <User key={item.id} name = {item.first_name + " " + item.last_name}
																		id = {item.id}
																		user_link = {user_link}
																		photo = {item.photo_100}
																		online = {item.online}
																		is_closed = {item.is_closed}
																		city = {'city' in item ? item.city.title : 'не указан'}/> })}</div>
			</div>
			: <p id="loader"><img src={loader} /></p>
			}
		</div>
			)
}

export default Friends;