import React, {useState, useEffect} from 'react'
import './Header.css'
import {NavLink} from 'react-router-dom'
import getQuery from './../api'

const Header = (props) => {
	let [isData, setIsData] = useState(false);
	let [data, setData] = useState([]);

	useEffect(() => {
		if (localStorage.getItem('login')) {
				let method = 'users.get';
				let params = `user_ids=${localStorage.getItem('id')}&fields=photo_max_orig`;
				getQuery(method, params).then(data => {
						props.setData(data.response[0]);
						setData(data.response[0]);
						setIsData(true);
				})
			}
	}, [data.id]);
		return(
			<div className="header">
			<h3>Хотите узнать о ком-то в вк? <NavLink to="/search">Вперёд!</NavLink>
				{!isData ? null : <p style={{display:'inline-block', float:'right', margin:0, marginRight:20}}>{props.idLogin.data.first_name}</p>}
			</h3>
			</div>
		)
}

export default Header