import React from 'react';
import {NavLink} from 'react-router-dom';
import './User.css'

const User = (props) => {
	//debugger;
	var secret = props.is_closed === true ? "Закрытый профиль" : "";
	return (
		<div className="user_search">
			<ul>
				<li key={props.id}>
					<NavLink onClick={props.user_link} className="new_info"  id={props.id} to={/user/ + props.id}>
						<h3>{props.name}<span></span><font size="2" color="#69E563">{secret}</font></h3>
						<img src={props.photo} alt=''/>
						{!props.online ? null : <div className='online'></div>}
							<div>
								<h4 id={props.id}>Город: {props.city}</h4>
							</div>
					</NavLink><hr></hr>
				</li>
			</ul>
		</div>
		)
}

export default User;