import React, {useState, useEffect} from 'react';
import getQuery from './../api'
import {Redirect} from 'react-router-dom'
import {TransitionGroup, CSSTransition} from 'react-transition-group'


const Data = (props) => {
	let [hide, setHide] = useState(false);
	let [data, setData] = useState([]);

	useEffect(() => {
		let method = 'database.getCities';
		let params = `q=${props.city.city}&country_id=${props.id_country}`;
		getQuery(method, params).then(data => {
			props.setCities(data.response.items);
			setHide(true);
			setData(data.response.items);
		})
	}, [data.count, props.cityName]);

	let usersLoad = (e, id) => {
		e.preventDefault();
		let method = 'users.search';
		let params = `q=${props.search.input_name}&country=${props.id_country}&city=${id}&count=50&fields=photo_100,first_name,last_name,city,country`;
		getQuery(method, params).then(data => {
			props.setUsers(data.response.items);
			setData(data.response.items);
		})
	}

	let cityLoad = (e) => {
		//debugger
		e.preventDefault();
		let city = e.currentTarget.getAttribute('title');
		let id = e.currentTarget.getAttribute('id');
		setHide(false);
		props.setCityId(id);
		props.setCity(city);
		usersLoad(e, id);
	}
		if(!props.cities) return null;
		let style = {display: "inline-block"};
		return (
			<div id='city_count' style={style}>
				{!localStorage.getItem('login') ? <Redirect to='/' />
				: hide && <TransitionGroup component="div">{props.cities.cities.map((item) => {return <CSSTransition
																						classNames="city"
																						timeout={1000}
																						key={item.id}
																						>
																						<City {...props} 
																						style={style} cityLoad={cityLoad} city={item.title}
																						id={item.id} /></CSSTransition>})}</TransitionGroup>
				}
			</div>
			)
}

const City = (props) => {
	let style = props.style;
	return (<div  id={props.id} className={props.city} >
				<a style={style} onClick={props.cityLoad} id={props.id} title={props.city} href="#">{props.city}</a>
			</div>)
}

export default Data