import React from 'react';
import {NavLink} from 'react-router-dom';
import './User.css'
import iphone from './../iphone.png'
import android from './../android.png'

const Users = (props) => {
	//debugger;
	var b_date = {"1": "января",
	"2": "февраля",
	"3": "марта",
	"4": "апреля",
	"5": "мая",
	"6": "июня",
	"7": "июля",
	"8": "августа",
	"9": "сентября",
	"10": "октября",
	"11": "ноября",
	"12": "декабря",}
	var name = props.user.first_name + " " + props.user.last_name;
	var online = props.user.online ? 'онлайн' : 'не в сети';
	var ava = props.user.photo_max_orig;
	const options = {
			 year: 'numeric',
			 month: 'long',
			 day: 'numeric',
			 hour: 'numeric',
			 minute: 'numeric',
			 second: 'numeric'
			}
	let now = Date.now() / 1000;
	let where = !props.user.last_seen ? 0 : (now - props.user.last_seen.time);
	var UnixTime = !props.user.last_seen ? 0 :props.user.last_seen.time;
	let date_now = where / 60;
	var date = new Date(UnixTime*1000);
	let status = !props.user.status ? '' : props.user.status;
	var time = new Intl.DateTimeFormat('ru-RU', options).format(date);
	var lastseen = time.toLocaleString();
	var str = !props.user.bdate ? "Не указана" : props.user.bdate.split("");
	console.log(str);
	var bir_date = "";
	if (str[2] === ".") {
		if ((str[4] === ".") || !str[4]) {
			bir_date =  str[3];
		} else {
			bir_date = str[3] + str[4];
		}
	} else if (str[1] === ".") {
		if (str[3] === ".") {
			bir_date =  str[2];
		} else {
			bir_date = str[2] + str[3];
		}
	}
	var date_1 = b_date[bir_date];
	if (str[1] === ".") {
		var bdate = str[0] + " " + date_1;
	} else {
		var bdate = str[0] + str[1] + " " + date_1;
	}
	var sex = props.user.sex === 2 ? 'Мужской' : 'Женский';
	var country = !props.user.country ? "Не указана" : props.user.country.title;
	var city = !props.user.city ? "Не указан" : props.user.city.title;
	var tele = props.user.mobile_phone;
	if(!props.user.counters) {
		var friends = 0;
		var photo = 0;
		var video = 0;
		var pages = 0;
		var followers = 0;
	} else {
		var friends = props.user.counters.friends;
		var photo = props.user.counters.photos;
		var video = props.user.counters.videos;
		var pages = props.user.counters.pages;
		var followers = props.user.counters.followers;
	}
	let style = {
		height: 20,
		width: 20,
		marginRight: 0,
		float: 'none',
		marginLeft: 10
	}
	let img = null;
	if (!props.user.last_seen) {
		img = null;
	} else if (props.user.last_seen.platform === 2) {
		img = <img src={iphone} style={style} alt=''/>;
	} else if (props.user.last_seen.platform === 4) {
		img = <img src={android} style={style} alt=''/>;
	}

	let last_time = null;
	if (!props.user.online) {
		if (date_now < 60) {
			last_time = Math.ceil(date_now) + ' минут назад';
		} else if ((date_now >= 60) && (date_now < 120)){
			last_time = 'час назад';
		} else if ((date_now >= 120) && (date_now < 180)){
			last_time = '2 часа назад';
		} else if ((date_now >= 180) && (date_now < 240)){
			last_time = '3 часа назад';
		} else if ((date_now >= 240) && (date_now < 300)){
			last_time = '4 часа назад';
		} else if ((date_now >= 300) && (date_now < 360)){
			last_time = '5 часов назад';
		} else if ((date_now >= 360) && (date_now < 420)){
			last_time = '6 часов назад';
		} else if ((date_now >= 420) && (date_now < 480)){
			last_time = '7 часов назад';
		} else if ((date_now >= 480) && (date_now < 540)){
			last_time = '8 часов назад';
		} else if ((date_now >= 540) && (date_now < 600)){
			last_time = '9 часов назад';
		} else if ((date_now >= 600) && (date_now < 660)){
			last_time = '10 часов назад';
		} else if ((date_now >= 660) && (date_now < 1400)){
			last_time = 'более 10 часов назад';
		}
	} else {
		last_time = null;
	}
	return (
		<div  id="profile">
			<div >
				<h3 id="name" >{ name }<span ></span></h3>
				<p><font id="online" size="3" color="#69E563">{ online } {!props.user.online_mobile ? null : <img src={android} style={style} alt=''/>}</font></p>
			</div>
			<p id="lastseen">Последний раз был(а) в сети: <font size="4" color="#69E563">{ lastseen }</font><font style={{marginLeft:10}} size="4" color="#398EDF">{last_time}{img}</font></p>
			<p id="status">Статус: <font size="4" color="#69E563">{ status }</font></p>
			<p className="inf_us"><img id="photo" src={ava} alt=""/></p>
			<p id="bdate">Дата рождения: <font size="4" color="#69E563">{ bdate }</font></p>
			<p id="sex">Пол: <font size="4" color="#69E563">{ sex }</font></p>
			<p id="country">Страна: <font size="4" color="#69E563">{ country }</font></p>
			<p id="city">Город: <font size="4" color="#69E563">{ city }</font></p>
			<NavLink id="friends" to={'/friends/' + props.user.id} href="#">Количество друзей: <font size="4" color="#69E563">{ friends }</font></NavLink>
			<p id="tele">Мобильный телефон: <font size="4" color="#69E563">{ tele }</font></p>
			<NavLink id="load_photos" to={'/photos/' + props.user.id}>Фотографий: <font size="4" color="#69E563">{ photo }</font></NavLink><br></br>
			<NavLink id="load_videos" to={'/home'} href="#">Видеозаписей: <font size="4" color="#69E563">{ video }</font></NavLink><br></br>
			<NavLink id="load_subscriptions" to={'/pages/' + props.user.id} href="#">Подписок: <font size="4" color="#69E563">{ pages }</font></NavLink><br></br>
			<NavLink id="load_followers" to={'/followers/' + props.user.id} href="#">Подписчиков: <font size="4" color="#69E563">{ followers }</font></NavLink><br></br>
		</div>
		)
}

export default Users;