import {connect} from 'react-redux';
import Pages from './../Pages/Pages';
import {setPagesAC} from './../redux/pageReducer'
import { withRouter } from 'react-router-dom';

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setPages: (users) => {
    	dispatch(setPagesAC(users));
    }
  }
}

let WithUrlData = withRouter(Pages)

const PagesContainer = connect(mapStateToProps, mapDispatchToProps)(WithUrlData);

export default PagesContainer;