import * as axios from 'axios';

const instance = axios.create({
	baseURL: 'https://api.vk.com/method/'
})

let getQuery = async (method, params, token) => {
	const data = await instance.get(`${method}?${params}&access_token=${localStorage.getItem('key')}&v=5.103`);
	return data.data;
}

export let loginApi = (e) => {
	e.preventDefault();
	window.location = 'https://oauth.vk.com/authorize?client_id=7211828&display=popup&redirect_uri=http://192.168.0.103:3000/redir&scope=friends,photos&response_type=code&v=5.103';

}

export let loginWithToken = (red) => {
	let token = `https://oauth.vk.com/access_token?client_id=7211828&client_secret=dmIrmIbrf1pdRjUf9f7d&redirect_uri=http://192.168.0.103:3000/redir&code=${red[1]}`;
	return axios.get(token).then(response => {
		return response.data;
	})
}

export default getQuery;