import React, {useState, useEffect} from 'react'
import './../Header/Header.css'
import {NavLink} from 'react-router-dom'

const Home = (props) => {
	useEffect(() => {
		props.setInputName('');
		props.setCity('');
		props.setCityId('');
		props.setCountry('');
	}, [])
	return(
		<div className="header">
			<h3>Хотите узнать о ком-то в вк? <NavLink to="/search">Вперёд!</NavLink></h3>
		</div>
	)
}

export default Home