import {connect} from 'react-redux';
import Video from './../Video/Video';
import { withRouter } from 'react-router-dom';
import {setVideosAC} from './../redux/videosReducer'

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setVideos: (users) => {
    	dispatch(setVideosAC(users));
    }
  }
}

let WithUrlData = withRouter(Video)

const VideoContainer = connect(mapStateToProps, mapDispatchToProps)(WithUrlData);

export default VideoContainer;