import React from 'react';
import './../Search/Search.css';
import {CSSTransition} from 'react-transition-group'

const Country = (props) => {
	//debugger
	return (
		<ul id="menu">
			<li><a className="butt" onClick={props.showMenu} href="#">Расширенный поиск &#9660;</a></li>
			<CSSTransition 
			in={props.hide}
			timeout={750}
			classNames={'show_menu'}
			mountOnEnter
			unmountOnExit>
				<ul ref={props.show} style={props.style} id="hide">
					<li><input id="input" value={props.country.country} onBlur={props.loadCountry} onChange={props.inputs1} placeholder ="Страна" type="text" name=""/></li>
					<li><input id="input" value={props.city.city} onChange={props.i_city} placeholder ="Город" type="text" name=""/></li>
				</ul>
			</CSSTransition>
		</ul>
		)
}

export default Country;