import {connect} from 'react-redux';
import Followers from './../Followers/Followers';
import {setFollowersAC} from './../redux/followersReducer'
import {setUserIdAC} from './../redux/userIdReducer'
import { withRouter } from 'react-router-dom';

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setFollowers: (followers) => {
    	dispatch(setFollowersAC(followers));
    },
    setUserId: (user_id) => {
    	dispatch(setUserIdAC(user_id));
    }
  }
}

let WithUrlData = withRouter(Followers)

const FollowersContainer = connect(mapStateToProps, mapDispatchToProps)(WithUrlData);

export default FollowersContainer;