import React, {useState, useEffect} from 'react';
import './Pages.css'
import getQuery from './../api'
import loader from './../loader.gif'
import {Redirect} from 'react-router-dom'
import {NavLink} from 'react-router-dom'

const Pages = (props) => {
	let [isLoad, setIsLoad] = useState(false);
	let [data, setData] = useState([]);

		useEffect(() => {
			setIsLoad(false);
			let method = 'groups.get';
			let params = `extended=1&user_id=${props.userId.user_id || props.match.params.userId}&count=1000&fields=photo_100,name`;
			getQuery(method, params).then(data => {
				props.setPages(data.response.items);
				setIsLoad(true);
				setData(data.response.items);
				document.title = 'Подписки пользователя ' + props.userInfo.user_info.first_name + ' ' + props.userInfo.user_info.last_name;
			})
		}, [data.count])
		if(!props.pages.pages) return null;
		let res = props.pages.pages;
		console.log(res);
	return(
		<div className="pages">
			{!localStorage.getItem('login') 
			? <Redirect to='/' />
			: isLoad ? <div className="search_user">
			<div id='home'><button onClick={props.history.goBack}>&#60; Назад</button>
					<NavLink to='/home'>На главную</NavLink>
				</div>
			<div>{ res.map((item) => { return <Page key={item.id} name = {item.name}
																	id = {item.id}
																	photo = {item.photo_100}
																	id_user = {props.userId.user_id}/> }) }</div>
			</div>
			: <p id="loader"><img src={loader} /></p>
			}
		</div>
			)
}

const Page = (props) => {
	return (
		<div>
			<h3>{props.name}</h3>
				<a className="id_group" id={props.id} href="#">
					<img src={props.photo} alt=''/>
				</a><hr></hr>
		</div>	
		)
}

export default Pages;