import {connect} from 'react-redux';
import Home from './../Home/Home';
import {setInputNameAC} from './../redux/searchReducer'
import {setCityAC} from './../redux/cityReducer'
import {setCountryAC} from './../redux/countryReducer'
import {setCityIdAC} from './../redux/cityIdReducer'
import { withRouter } from 'react-router-dom';

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setInputName: (input_name) => {
    	dispatch(setInputNameAC(input_name));
    },
    setCity: (input_name) => {
    	dispatch(setCityAC(input_name));
    },
    setCityId: (city_id) => {
    	dispatch(setCityIdAC(city_id));
    },
    setCountry: (city_id) => {
    	dispatch(setCountryAC(city_id));
    }
  }
}


const HomeContainer = connect(mapStateToProps, mapDispatchToProps)(Home);

export default HomeContainer;