const SET_INPUT_NAME = 'SET_INPUT_NAME';

let initialState = {
	input_name: ""
}

const searchReducer = (state = initialState, action) => {
	
	switch (action.type) {
		case SET_INPUT_NAME: {
			return {...state, input_name: action.input_name};
		}

		default:
			return state;
	}
}


export const setInputNameAC = (input_name) => ({type: SET_INPUT_NAME, input_name});


export default searchReducer;