import {connect} from 'react-redux';
import Friends from './../Friends/Friends';
import {setFriendsAC} from './../redux/friendsReducer'
import {setUserIdAC} from './../redux/userIdReducer'
import { withRouter } from 'react-router-dom';

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setFriends: (friends) => {
    	dispatch(setFriendsAC(friends));
    },
    setUserId: (user_id) => {
    	dispatch(setUserIdAC(user_id));
    }
  }
}

let WithUrlData = withRouter(Friends)

const FriendsContainer = connect(mapStateToProps, mapDispatchToProps)(WithUrlData);

export default FriendsContainer;