import React from 'react';
import './Video.css';
import getQuery from './../api'

class Video extends React.Component {


		componentDidMount = () => {
		let method = 'video.get';
		let params = `owner_id=${this.props.userId.user_id || this.props.match.params.userId}&count=200&extended=1`;
		getQuery(method, params).then(data => {
			this.props.setVideos(data.response.items);
		})
		}
	render() {
		if(!this.props.videos.videos) return null;
		let res = this.props.videos.videos;
		console.log(res);
		const show = () => {
			this.children[0].style.display = 'block';
		}
		const no_show = () => {
			this.style.display = 'none';
		}
	return(
		<div>
		{/* { res.map((item) => { return <div key={item.id} className='container'>
        			<FancyVideo
          				source={item.player}
          				poster={item.image[2].url}
          				id={"sintel"}
         			/>
				</div>}) } */}
		</div>
		)}
}


export default Video;