import {connect} from 'react-redux';
import Userprofile from './../Userprofile/Userprofile';
import {setUserInfoAC} from './../redux/userInfoReducer'
import {setWallAC} from './../redux/wallReducer'
import { withRouter } from 'react-router-dom';

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setUserInfo: (user_info) => {
    	dispatch(setUserInfoAC(user_info));
    },
    setWall: (user_info) => {
    	dispatch(setWallAC(user_info));
    }
  }
}

let WithUrlData = withRouter(Userprofile)

const UserProfileContainer = connect(mapStateToProps, mapDispatchToProps)(WithUrlData);

export default UserProfileContainer;