const SET_ID = 'SET_ID';

let initialState = {
	user_id: ''
}

const userIdReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_ID: {
			return {...state, user_id: action.user_id};
		}

		default:
			return state;
	}
}

export const setUserIdAC = (user_id) => ({type: SET_ID, user_id});

export default userIdReducer;