const SET_WALL = 'SET_WALL';

let initialState = {
	wall: []
}

const wallReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_WALL: {
			return {...state, wall: action.wall};
		}

		default:
			return state;
	}
}

export const setWallAC = (wall) => ({type: SET_WALL, wall});

export default wallReducer;