const SET_PAGES = 'SET_PAGES';

let initialState = {
	pages: ''
}

const pageReducer = (state = initialState, action) => {
	// debugger
	switch (action.type) {
		case SET_PAGES: {
			return {...state, pages: action.pages};
		}

		default:
			return state;
	}
}

export const setPagesAC = (pages) => ({type: SET_PAGES, pages});

export default pageReducer;