import {connect} from 'react-redux';
import Redir from './../Redir/Redir';
import { withRouter } from 'react-router-dom';
import {setIdAC} from './../redux/idLoginReducer'

const mapStateToProps = (state) => {
  return state;
}

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch: dispatch,
    setId: (id) => {
      dispatch(setIdAC(id));
    }
  }
}

let WithUrlData = withRouter(Redir)

const RedirContainer = connect(mapStateToProps, mapDispatchToProps)(WithUrlData);

export default RedirContainer;